import React, { createElement } from 'react'
import { Link, Button } from '../components'
import rehypeReact from 'rehype-react'

const renderAst = new rehypeReact({
  createElement,
  components: {
    button: Button,
  }
}).Compiler

export default ({ data }) => {
  const { site: { siteMetadata: { title, description }}, allMarkdownRemark } = data
  if(!allMarkdownRemark) return false
  const published = allMarkdownRemark.edges.filter(({ node}) => node.frontmatter.status !== 'draft')
    .sort((a, b) => Date.parse(b.node.frontmatter.date) - Date.parse(a.node.frontmatter.date))
  return (
    <ul>
      {published.map(({ node }) => (
        <li key={node.id}>
          <Link to={`/${node.fields.slug}/`}>
            {node.frontmatter.title || 'NOTITLE'}
          </Link>
          {'  '}{node.frontmatter.date}
        </li>
      ))}
    </ul>
  )
}
export const query = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark {
      totalCount
      edges {
        node {
          fields {
            slug
          }
          id
          frontmatter {
            status
            title
            subtitle
            date(formatString: "DD MMMM, YYYY")
          }
          excerpt
        }
      }
    }
  }
`



